.card  {
  margin: 0 auto;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid var(--gray-elements);
  width: 200px;
  text-align: center;
  display: flex;
  flex-direction: column;

  &__picture {
    display: flex;
    justify-content: center;
    margin: 0 0 40px;

    &-img {
      width: 160px;
      height: 134px;
    }
  }

  &__product {
    margin-bottom: 16px;
    text-align: start;

    &-name {
      margin-bottom: 4px;
      font-size: 12px;
      font-weight: 500;
      line-height: 18px;
      color: var(--main-accent);
    }

    &-code {
      font-size: 10px;
      line-height: 14px;
      color: var(--secondary);
    }
  }

  &__raiting {
    display: flex;
    text-align: center;
    justify-content: space-between;
    margin-bottom: 24px;

    &-reviewes {
      font-size: 10px;
      line-height: 16px;
      color: var(--main-accent);
    }
  }


  &__price {
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;

    &-name {
      font-size: 12px;
      line-height: 18px;
      color: var(--secondary);
    }

    &-price {
      color: var(--main-accent);
      font-size: 16px;
      font-weight: 700;
      line-height: 18px;
    }
  }

  &__link-buy {
    width: 166px;
    border-radius: 5px;
    background-color: var(--blue-accent);

    text-transform: uppercase;
    text-decoration: none;
    color: var(--white);
    line-height: 40px;
    font-size: 14px;
    font-weight: 700;

    &:hover {
      border: 1px solid var(--blue-accent);
      background-color: var(--white);
      color: var(--blue-accent);
    }
  }
}
