* {
  box-sizing: border-box;
}

body {
  --blue-accent: #00acdc;
  --main-accent: #060b35;
  --secondary: #616070;
  --white: #fff;
  --gray-elements: #f3f3f3;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
}

h2, p {
  margin: 0;
}

.card {
  border: 1px solid var(--gray-elements);
  text-align: center;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  display: flex;
}

.card__picture {
  justify-content: center;
  margin: 0 0 40px;
  display: flex;
}

.card__picture-img {
  width: 160px;
  height: 134px;
}

.card__product {
  text-align: start;
  margin-bottom: 16px;
}

.card__product-name {
  color: var(--main-accent);
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: var(--secondary);
  font-size: 10px;
  line-height: 14px;
}

.card__raiting {
  text-align: center;
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__raiting-reviewes {
  color: var(--main-accent);
  font-size: 10px;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  padding: 0;
  display: flex;
}

.card__price-name {
  color: var(--secondary);
  font-size: 12px;
  line-height: 18px;
}

.card__price-price {
  color: var(--main-accent);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__link-buy {
  background-color: var(--blue-accent);
  text-transform: uppercase;
  color: var(--white);
  border-radius: 5px;
  width: 166px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
}

.card__link-buy:hover {
  border: 1px solid var(--blue-accent);
  background-color: var(--white);
  color: var(--blue-accent);
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

